.index-news {
  min-height: 700px;
  display: flex;
  margin: 0;
  .col-md-3{
    display: flex;
    padding: 0;
  }
  .ind-news {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    @include make-flex-div();
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    padding: 15px;
    .news-date {
      z-index: 1;
      color: $color-brand-primary;
      font-family: $font-family-bebas;
      text-transform: uppercase;
      font-size: 22px;
      letter-spacing: 3px;
      span {
        color: #fff;
        font-family: $font-family-work-sans;
        margin-left: 20px;
        letter-spacing: 0;
        font-size: 16px;
      }
    }
    .news-title {
      z-index: 1;
      color: $owl-news-body-text;
      margin-bottom: 15px;
      font-family: $font-family-work-sans;
      display: block;
      font-size: 20px;
      width: 100%;
    }
    .news-text,
    .news-text-hover {
      color:$owl-news-body-text;
      z-index: 1;
      font-family: $font-family-work-sans;
      font-size: 15px;
    }

  }
}

.index-social-row {
  background: #3b3838;
  padding: 25px 0;
  text-align: center;
  a {
    display: inline-block;
    margin: 0 15px;
  }
  i {
    font-size: 6vw;
  }
}


/**
